}

body {
  background: #FFF;
  line-height: 1.4;
  color: #444;
  height: 100%;
  font-size: 13px;
  min-width: 320px;
  font-family: 'Open Sans', Arial, -apple-system, BlinkMacSystemFont, 'Oxygen', sans-serif;
}

body a {
  color: #444;
  text-decoration: none;
}

