.btn {
  height: 48px;
  color: #fff;
  background-color: #2471be;
  text-align: center;
  padding: 0 30px;
}

.btn:disabled {
  background-color: rgba(36, 113, 190, 0.4);
}

.btn_bold {
  font-weight: 600;
}

.btn_remove {
  background-color: transparent;
  color: #444;
  border: 1px solid #dfdfdf;
}

.btn_remove:disabled {
  color: rgba(68, 68, 68, 0.54);
  background-color: transparent;
}

.btn_w100 {
  width: 100%;
}

.btn_white {
  background-color: #fff;
  color: #2471be;
  border: 1px solid #2471be;
}

.btn_orange {
  height: 40px;
  background-color: #f7941e;
}

.btn-icon {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: none;
  width: 24px;
  height: 24px;
  transition: color .3s;
}

.btn-icon:before {
  position: absolute;
  content: '';
  top: -6px;
  left: -6px;
  bottom: -6px;
  right: -6px;
}

.btn-icon .icon {
  display: block;
  font-size: 18px;
}

.btn-icon .icon-icons_close {
  font-size: 12px;
}

.btn-icon_gray {
  color: #a7a7aa;
}

.btn-icon.is-active {
  color: #2471be;
}

.btn-icon.is-active .red-dot {
  opacity: 1;
  visibility: visible;
}

.btn-icon_bg.is-active:before {
  background-color: rgba(36, 113, 190, 0.1);
}

.btn-action {
  width: 100%;
  height: 64px;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: 1px solid #2471be;
  color: #2471be;
}

.btn-action__icon .icon {
  display: block;
  max-width: 100%;
}

.btn-action__text {
  font-weight: 600;
  margin-top: 4px;
}

.red-dot {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 10px;
  height: 10px;
  border: 2px solid #fff;
  border-radius: 50%;
  background-color: #eb5757;
  opacity: 0;
  visibility: hidden;
}

