.map-popup__wrap {
  background-color: #fff;
  position: relative;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}

.map-popup__wrap:before, .map-popup__wrap:after {
  content: '';
  position: absolute;
  left: 50%;
  top: 100%;
  width: 20px;
  height: 20px;
  transform: rotate(45deg);
  margin-top: -11px;
  transform-origin: center;
}

.map-popup__wrap:before {
  background-color: #fff;
}

.map-popup__wrap:after {
  box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.25);
  z-index: -1;
}

.events-item {
  position: relative;
  padding: 16px 0;
  border-bottom: 1px solid #dfdfdf;
