.events-item__input {
  display: none;
}

.events-item__input:checked ~ .events-item__checkbox {
  border-color: #f7941e;
}

.events-item__input:checked ~ .events-item__checkbox:after {
  opacity: 1;
}

.events-item__input:checked ~ .events-item__bg {
  opacity: 1;
}

.events-item__checkbox {
  position: absolute;
  top: 22px;
  right: 24px;
  display: block;
  width: 22px;
  height: 22px;
  background-color: #fff;
  border: 2px solid #c4c4c4;
  transition: border-color .3s;
  opacity: 0;
  visibility: hidden;
}

.events-item__checkbox:after {
  content: '';
  position: absolute;
  left: 4px;
  top: 50%;
  transform: translateY(-50%);
  width: 10px;
  height: 10px;
  background-color: #f7941e;
  opacity: 0;
  transition: opacity .3s;
}

.events-item.is-removable .events-item__checkbox {
  opacity: 1;
  visibility: visible;
}

.events-item__bg {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(36, 113, 190, 0.05);
  z-index: -1;
  opacity: 0;
  transition: opacity .3s;
}

